<template>
  <div class="guild" style="padding-top: 44px;">
    <back-header title="公会中心" />
    <form @submit.prevent="getList" class="search">
      <div class="input">
        <img src="./ic_search@2x.png" alt="">
        <input v-model="searchKey" type="text" placeholder="搜索公会ID/名称">
        <img @click.stop="searchKey=''" src="./ic_delete@2x.png" alt="">
      </div>
      <button type="submit">搜索</button>
    </form>
    <h4 class="title"><img src="./ic@2x.png" alt="">公会列表</h4>
    <scroll clickable ref="scroll" class="guild-scroll" :is-finished="finished" @scrollToEnd="onLoad">
      <guild-item v-for="item in list" :item="item" :key="item.id" />
    </scroll>
  </div>
</template>

<script>
import GuildItem from '../../components/guild-item/guild-item'
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'
import { mapMutations } from 'vuex'

// const PAGE_SIZE = 1

export default {
  name: 'guild',
  components: {
    GuildItem,
    BackHeader,
    Scroll
  },
  data() {
    return {
      list: [],
      loading: false,
      finished: true,
      searchKey: '',
      page: 1
    }
  },
  mounted() {
    this.getList()
    this.changeHistory([])
  },
  methods: {
    ...mapMutations(['changeHistory']),
    search() {
      const list = []
      for (let i = 0; i < 9; i++) {
        list.push({
          id: Date.now().toString().slice(10) + i,
          avatar: 'https://dss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=1354268575,1268995723&fm=26&gp=0.jpg',
          title: '一起来赚钱',
          desc: '丰厚奖金，多劳多得，快来加入',
          member: 20
        })
      }
      this.list = this.list.concat(list)
      this.loading = false
      this.$nextTick(() => {
        this.$refs.scroll.refresh()
      })
    },
    getList() {
      post('/webapi/guild/getGuildList', {
        keyword: this.searchKey
      })
        .then(({ data: { data } }) => {
          this.list = data
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        })
    },
    onLoad() {
      if (this.loading) return
      this.loading = true
      setTimeout(() => {
        this.search()
      }, 1000)
    }
  }
}
</script>

